.major-image {
  width: 87.5vw;

  margin: 0 auto;
}

.major-image__captions {
  display: flex;
  padding-top: 40px;
  justify-content: space-between;
}

.major-image__caption {
  color: $basic-black;
  font-size: 18px;
  font-weight: 700;
  line-height: 23.4px;
}

.major-image__image {
  background: $green;
  border-radius: 50%;
}
