/* Общие стили. Каркас
   ========================================================================== */

img {
  width: 100%;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.content-box-component {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
}
